<style lang="scss" scoped>
.container_wrap{
  .header{
    background-color: #304156;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    .header_title{
      padding: 6px 0 0 20px;
      font-size: 20px;
      color: #F3F4F9;
      font-weight: 800;
      cursor: pointer;
    }
    .navigation_right {
        padding: 11px 50px 0 0;
        .el-icon-user-solid {
            font-size: 20px;
            color: #1B97F6;
        }
        .person_head{
          color: #F3F4F9;
          font-size: 25px;
        }
    }
  }
  .container{
    overflow: hidden;
    .container_aside{
      background-color: #304156;
      .aside_menu{
        
      }
    }
    .container_main{
      height: 100%;
      background-color: #E1E2E7;
    }
  }
}
</style>
<style>
.el-submenu .el-menu-item {
    min-width: 100px;
    padding: 0 10px !important;
}
.el-menu {
    border-right: 0;
    text-align: left;
    padding-left: 20px;
}
</style>
<template>
  <el-container class="container_wrap" ref="container_wrap">
    <el-header class="header" height="45px">
      <div class="header_title" @click="toIndex"> 
        XX公司人事管理系统
      </div>
      <div class="navigation_right">
          <!-- <a v-if="loginFlag" class="navigation_text" @click="changeloginBoxFlag">登录/注册</a> -->
          <el-dropdown>
              <el-badge :value="notice" class="item" type="primary">
                  <i class="el-icon-user-solid person_head"></i>
              </el-badge>
              <el-dropdown-menu >
                  <el-dropdown-item @click.native="toPersonCenter">个人中心</el-dropdown-item>
                  <el-dropdown-item @click.native="toMyDepartment">我的部门</el-dropdown-item>
                  <el-dropdown-item @click.native="notifyNone">最新消息({{notice}})</el-dropdown-item>
                  <el-dropdown-item divided @click.native="exitLogin">退出登录</el-dropdown-item>
              </el-dropdown-menu>
          </el-dropdown>
      </div>
    </el-header>
    <el-container class="container">
      <el-aside width="150px" class="container_aside window_scroll">
        <el-menu class="el-menu-vertical-demo aside_menu" :unique-opened="true"
                background-color="#304156" text-color="#BFCBD9" :router="true" default-active="/workdest">
          <div v-for="item in routers" :key="item.id">
            <el-menu-item :index="item.url" :route="item.url" v-if="item.children.length<=0" :disabled="item.status=='0'">{{item.name}}</el-menu-item>
            <el-submenu :index="String(item.id)"  @click="clickMenu(keys)" v-else>
              <template slot="title" >
                {{item.name}}
              </template>
              <template>
                <div @click="clickMenu(keys)" v-for="keys in item.children" :key="keys.id">
                  <el-menu-item :index="keys.url" :route="keys.url" :disabled="keys.status=='0'">{{keys.name}}</el-menu-item>
                </div>
              </template>
            </el-submenu>
          </div>
          <!-- <el-menu-item index="11">测试路由</el-menu-item> -->
        </el-menu>
      </el-aside>
      <el-main class="container_main window_scroll">
          <router-view ></router-view>
      </el-main>
      <el-backtop target=".container_main"></el-backtop>
    </el-container>
    
</el-container>
</template>
<script>
export default {
  name:'home',
  data(){
    return{
      notice:5,
      routers: [],
      person:{
        age: "22",
        create_time: "2018-05-23 21:22:06",
        email: "523179414@qq.com",
        id: "1",
        last_login_time: "2019-11-15 16:07:37",
        password: "1dc99c9d72c158159cbebccdc1af1885",
        phone: "187888899991",
        salt: "53b0b184c03cf848ac57e5015999ec44",
        sex: "1",
        status: "1",
        update_time: "2019-11-12 22:45:46",
        user_id: "1",
        username: "admin",
      }
    }
  },
  created(){
    this.filterRouter()
  },
  mounted(){
  },
  methods:{
    notifyNone(){
        this.$notify({
            message: '不好意思，该功能暂未开发完成',
            position: 'top-right',
            type: 'warning',
            duration: 1500,
        })
    },
    clickMenu(keys){
      console.log(keys.id+":"+keys.url)
    },
    toMyDepartment(){
      this.$router.push({
        path: '/myDepartment',
        query:{
          id:this.person.id,
          title: '我的部门'
        }
      })
    },
    toPersonCenter(){
      this.$router.push({
        path: '/personCenter',
        query:{
          id:this.person.id,
          title: '个人中心'
        }
      })
    },
    toIndex(){
      this.$router.push({
        path:'/index',
      })
    },
    exitLogin(){
      this.$confirm('是否确定退出此账户','提示',{
        confirmButtonText: '我要退出',
        cancelButtonText: '我在想想',
        type: 'warning',
      }).then(() => {
         this.$axios.post('/pms_api/logout').then(res => {
          this.$message('成功退出')
          this.$getGlobal.deleteItem('accessToken')
        }).catch(err => {
          console.log('err',err);
          this.$message.warning('退出失败')
        })
        this.$router.push({
          path: '/login'
        })
      })
    },
    filterRouter(){ // 将后台返回的无规则路由进行树形处理
      //  get('/local_api/static/data.json').then(res => {
        get('/pms_api/menu').then(res => {
        // let routerList=this.$getGlobal.getItemToLocal('routers')
        console.log(res);
        let routerList=res.rows
        let routersTmp=[]
        if(routerList!=null&&routerList!=undefined&&routerList!=''){ // 找出最顶层
          for(let index in routerList){
            if(routerList[index].parentId==0){
              routersTmp.push(routerList[index])
              // console.log('最顶层routersTmp',routersTmp)
            }
          }
          console.log('最顶层routersTmp',routersTmp)
          for(let index in routersTmp){
            routersTmp[index].children=[]
          }
          for(let index in routerList){ // 找出除了最顶层的所有层
            if(routerList[index].parentId!=0){
              for(let idx in routersTmp){
                if(routersTmp[idx].id==routerList[index].parentId){
                  routersTmp[idx].children.push(routerList[index])
                  break;
                }
              }
            }
          }
          // 排序
          routersTmp.sort((pre,back) => {
            if(parseInt(pre.orderNum)<parseInt(back.orderNum)){
              return -1
            }else if(parseInt(pre.orderNum)>parseInt(back.orderNum)){
              return 1
            }else{
              return pre.url.localeCompare(back.url);
            }
          })
          for(let index in routersTmp){
            if(routersTmp[index].children){
              routersTmp[index].children.sort((pre,back) => {
                if(parseInt(pre.orderNum)<parseInt(back.orderNum)){
                  return -1
                }else if(parseInt(pre.orderNum)>parseInt(back.orderNum)){
                  return 1
                }else{
                  return pre.url.localeCompare(back.url);
                }
              })
            }
          }
          this.$getGlobal.setItem('routers',routersTmp)
          this.routers=routersTmp
        }
      }).catch(err => {
        console.log(err)
        this.$message.error('请求路由集合出错'+err.code)
      })
    }
  }
}
</script>




